import { AppDispatch, RootState } from "./store";
import { batchActions } from "redux-batched-actions";

import Interface, { JobState } from '../interfaces/az-iface';
import { actions as appActions, AppTab } from './app-state';
import { actions as serverActions } from './server-state';
import { actions as createDialogActions, CreateMode } from './create-dialog-state';
import { actions as errorDialogActions } from './error-dialog-state';
import { actions as logsDialogActions } from './logs-dialog-state';
import { Job, Status } from "../interfaces/az-iface";

export function loadDataFromServer() {
    return async function (dispatch: AppDispatch, getState: () => RootState) {
        const jobs = await Interface.getJobs();
        const templates = await Interface.getTemplates();
        const params = await Interface.getPossibleParameters();

        resetParameters()(dispatch, getState);
        dispatch(batchActions([
            serverActions.setJobs(jobs),
            serverActions.setTemplates(templates),
            serverActions.setParameters(params),
            serverActions.setLoaded(true),
        ]));
    }
}

export function resetParameters() {
    return async function (dispatch: AppDispatch, getState: () => RootState) {
        let newParamValues: { [key: string]: any } = {};
        for (let prop of getState().server.parameters) {
            newParamValues[prop.name] = prop.default;
        }
        dispatch(createDialogActions.setParameters(newParamValues));
    }
}

export function createTemplateOrJob(type: "template" | "job", name: string, username: string, password: string, params: { [key: string]: any }) {
    return async function (dispatch: AppDispatch, getState: () => RootState) {
        const status = await (type === "template" ? Interface.createTemplate : Interface.createJob)(name, username, password, params);
        if (status.ok) {
            dispatch(batchActions([
                createDialogActions.setVisible(false),
                appActions.setCurrentTab(type === "job" ? AppTab.JOBS_TAB : AppTab.TEMPLATES_TAB),
            ]));
            loadDataFromServer()(dispatch, getState);
        } else {
            dispatch(batchActions([
                errorDialogActions.setVisible(true),
                errorDialogActions.setErrorString(status.log!),
            ]));
        }
    }
}

export function deleteTemplateOrJob(type: "template" | "job", id: number) {
    return async function (dispatch: AppDispatch, getState: () => RootState) {
        const status = await (type === "template" ? Interface.deleteTemplate : Interface.deleteJob)(id);
        if (!status.ok) {
            dispatch(batchActions([
                errorDialogActions.setVisible(true),
                errorDialogActions.setErrorString(status.log!),
            ]));
        }
        loadDataFromServer()(dispatch, getState);
    }
}

export function editTemplate(id: number, name: string, username: string, password: string, params: { [key: string]: any }) {
    return async function (dispatch: AppDispatch, getState: () => RootState) {
        const currentTemplate = getState().server.templates.find(n => n.id === id)!;
        const handleStatus = (e: Status) => {
            if (!e.ok) {
                dispatch(batchActions([
                    errorDialogActions.setVisible(true),
                    errorDialogActions.setErrorString(e.log!),
                ]));
                return false;
            }
            return true;
        }
        if (currentTemplate.friendlyName !== name) {
            if (!handleStatus(await Interface.renameTemplate(currentTemplate.id, name))) {
                return;
            }
        }
        const workingParameters = Object.entries(params).filter(n => {
            if (n[0] in currentTemplate.parameters) {
                return n[1] !== currentTemplate.parameters[n[0]];
            }
            return true;
        });
        for (let elem of workingParameters) {
            if (!handleStatus(await Interface.updateTemplateParameter(currentTemplate.id, elem[0], elem[1]))) {
                return;
            }
        }
        dispatch(createDialogActions.setVisible(false));
        loadDataFromServer()(dispatch, getState);
    }
}

export function startEditTemplate(id: number) {
    return async function (dispatch: AppDispatch, getState: () => RootState) {
        const template = getState().server.templates.find(n => n.id === id)!;
        dispatch(batchActions([
            createDialogActions.setEditTemplateID(id),
            createDialogActions.setFriendlyName(template.friendlyName),
            createDialogActions.setFriendlyName(template.friendlyUsername),
            createDialogActions.setPassword(template.password),
            createDialogActions.setParameters(template.parameters),
            createDialogActions.setVisible(true),
            createDialogActions.setMode(CreateMode.TEMPLATE),
        ]));
    }
}

export function createFromTemplate(id: number) {
    return async function (dispatch: AppDispatch, getState: () => RootState) {
        const template = getState().server.templates.find(n => n.id === id)!;
        const date = new Date();
        const p = (e: any) => e.toString().padStart(2, '0');
        dispatch(batchActions([
            createDialogActions.setEditTemplateID(-1),
            createDialogActions.setFriendlyName(`${template.friendlyName} - ${p(date.getDate())}-${p(date.getMonth() + 1)}-${p(date.getFullYear())} ${p(date.getHours())}:${p(date.getMinutes())}:${p(date.getSeconds())}`),
            createDialogActions.setFriendlyUsername(template.friendlyUsername),
            createDialogActions.setPassword(template.password),
            createDialogActions.setParameters(template.parameters),
            createDialogActions.setVisible(true),
            createDialogActions.setMode(CreateMode.JOB),
        ]));
    }
}

export function openJob(job: Job) {
    return async function (dispatch: AppDispatch, getState: () => RootState) {
        const jobStatus = await Interface.getJobStatus(job.internalName);
        if (jobStatus === null) {
            // No job like this found.
            dispatch(batchActions([
                errorDialogActions.setVisible(true),
                errorDialogActions.setErrorString(`Job ${job.internalName} doesn't exist. Cannot check status`),
            ]));
            return;
        }

        dispatch(batchActions([
            logsDialogActions.setVisible(true),
            logsDialogActions.setFriendlyName(job.friendlyName),
            logsDialogActions.setPassword(job.password),
            logsDialogActions.setCreatedDate(new Date(job.timeCreated + new Date().getTimezoneOffset() * 60 * 1000).toLocaleString()),
            logsDialogActions.setLogs(jobStatus.jobLogs),
            logsDialogActions.setState(JobState[jobStatus.jobState]),
            logsDialogActions.setParameters(job.parameters),
        ]));
    }
}