import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { enableBatching } from 'redux-batched-actions';
import { JobState } from '../interfaces/az-iface';

export interface LogsDialogState {
    visible: boolean;
    state: JobState;
    friendlyName: string;
    friendlyUsername: string;
    password: string;
    logs: string;
    parameters: { [key: string]: any };
    createdDate: string;
};

const initialState: LogsDialogState = {
    visible: false,
    state: JobState.RUNNING,
    friendlyName: "",
    friendlyUsername: "",
    password: "",
    logs: "",
    parameters: {},
    createdDate: "",
};

export const slice = createSlice({
    name: 'logs-dialog',
    initialState,
    reducers: {
        setVisible: (state, action: PayloadAction<boolean>) => {
            state.visible = action.payload;
        },
        setState: (state, action: PayloadAction<JobState>) => {
            state.state = action.payload;
        },
        setFriendlyName: (state, action: PayloadAction<string>) => {
            state.friendlyName = action.payload;
        },
        setFriendlyUsername: (state, action: PayloadAction<string>) => {
            state.friendlyUsername = action.payload;
        },
        setPassword: (state, action: PayloadAction<string>) => {
            state.password = action.payload;
        },
        setParameters: (state, action: PayloadAction<{ [key: string]: any }>) => {
            state.parameters = action.payload;
        },
        setLogs: (state, action: PayloadAction<string>) => {
            state.logs = action.payload;
        },
        setCreatedDate: (state, action: PayloadAction<string>) => {
            state.createdDate = action.payload;
        },

    }
});

export const { reducer, actions } = slice;
export default enableBatching(reducer);
