import { Modal, ProgressIndicator, ProgressStep, StructuredListWrapper, StructuredListHead, StructuredListCell, StructuredListRow, StructuredListBody } from "carbon-components-react";
import { useCallback } from "react";
import { useDispatch } from "react-redux";
import { useShallowSelector } from "../utils";
import { actions as logsDialogActions } from '../redux/logs-dialog-state';
import { JobState } from "../interfaces/az-iface";
import { Download16 } from '@carbon/icons-react';

export default function LogsDialog() {
    const dispatch = useDispatch();

    const { visible, state, friendlyName, friendlyUsername, password, parameters, logs, createdDate } = useShallowSelector(state => state.logsDialog);
    const { parameters: configParameters } = useShallowSelector(state => state.server);

    const handleClose = useCallback(() => {
        dispatch(logsDialogActions.setVisible(false));
    }, [dispatch]);

    const stateToIndex = {
        [JobState.RUNNING]: 1,
        [JobState.COMPLETED]: 2,
        [JobState.ERRORED]: 2,
    };

    const renderValue = (e: any) => {
        if (typeof e === "boolean") {
            return e ? "true" : "false";
        } else return e;
    }

    const friendlyNameOfParams: { [key: string]: string } = {};
    for (let param of configParameters) {
        friendlyNameOfParams[param.name] = param.friendlyName;
    }

    const download = () => {
        const content = `Autozap job logs download
Job Name: ${friendlyName}
Creation date: ${createdDate}
Username: ${friendlyUsername}
Password: ${password}
Logs:
${logs}`;
        const blob = new Blob([content], {
            type: 'text/plain; charset=UTF-8',
        });
        const url = URL.createObjectURL(blob);
        const a = document.createElement("a");
        a.href = url;
        a.download = `${friendlyName}.txt`;
        document.body.appendChild(a);
        a.style.display = 'none';
        a.click();
        URL.revokeObjectURL(url);
        document.body.removeChild(a);
    }

    console.log(stateToIndex[state]);

    return (
        <Modal
            modalHeading={`${friendlyName} (created on ${createdDate})`}
            modalLabel="Job Info"
            passiveModal
            open={visible}
            onRequestClose={handleClose}
            className="modalLogsDialog"
        >
            <div>Username: {friendlyUsername}</div>
            <div>Password: {password}</div>

            <div className="modalLogsDialogContainer">
                <StructuredListWrapper>
                    <StructuredListHead>
                        <StructuredListRow head>
                            <StructuredListCell head style={{ width: "25%" }}>Key</StructuredListCell>
                            <StructuredListCell head style={{ width: "75%" }}>Value</StructuredListCell>
                        </StructuredListRow>
                    </StructuredListHead>
                    <StructuredListBody>
                        {Object.entries(parameters).map((e, i) => (
                            <StructuredListRow key={`renderObject-key-${i}`}>
                                <StructuredListCell noWrap>{friendlyNameOfParams[e[0]]}</StructuredListCell>
                                <StructuredListCell>{renderValue(e[1])}</StructuredListCell>
                            </StructuredListRow>
                        ))}
                    </StructuredListBody>
                </StructuredListWrapper>
                <div className="logs">
                    <Download16 className="download-button" onClick={() => download()} />
                    <code>
                        {logs}
                    </code>
                </div>
                <ProgressIndicator
                    vertical={false}
                    currentIndex={stateToIndex[state]}
                    spaceEqually={true}>
                    <ProgressStep
                        label={"Started"}
                    />
                    <ProgressStep
                        label="Running"
                    />
                    <ProgressStep
                        invalid={state === JobState.ERRORED}
                        label={state === JobState.ERRORED ? "Failed" : "Completed"}
                        secondaryLabel={state === JobState.ERRORED ? "The Job has failed." : "The Job is now complete."}
                    />
                </ProgressIndicator>
            </div>
        </Modal>
    );
}