import React, { useCallback } from 'react';
import { useDispatch } from 'react-redux';
import { useShallowSelector } from '../utils';
import {
    TextInput,
    NumberInput,
    Checkbox,
    Modal,
} from 'carbon-components-react';
import { ConfigParameter } from '../interfaces/az-iface';
import { actions as createDialogActions, CreateMode } from '../redux/create-dialog-state';
import { createTemplateOrJob, editTemplate } from '../redux/actions';

export default function CreateDialog() {
    const dispatch = useDispatch();

    const { parameters, visible, mode, friendlyName, friendlyUsername, password, editTemplateID } = useShallowSelector(state => state.createDialog);
    const isEditingTemplate = editTemplateID !== -1;

    const { parameters: parametersPrototype } = useShallowSelector(state => state.server);
    const updateUserInputInModal = useCallback((property: ConfigParameter, event: any) => {
        const newParameters = {
            ...parameters,
            [property.name]: property.type === "BOOLEAN" ? event : event.target.value,
        };
        dispatch(createDialogActions.setParameters(newParameters));
    }, [parameters, dispatch]);

    const handleCreate = useCallback(() => {
        if (!isEditingTemplate)
            dispatch(createTemplateOrJob(
                mode === CreateMode.JOB ? "job" : "template",
                friendlyName,
                password,
                friendlyUsername,
                parameters
            ));
        else
            dispatch(editTemplate(
                editTemplateID,
                friendlyName,
                password,
                friendlyUsername,
                parameters
            ));
    }, [dispatch, friendlyName, friendlyUsername, password, parameters, mode, editTemplateID, isEditingTemplate]);

    const handleInputFriendlyName = useCallback((e: any) => {
        dispatch(createDialogActions.setFriendlyName(e.target.value));
    }, [dispatch]);
    const handleInputFriendlyUsername = useCallback((e: any) => {
        dispatch(createDialogActions.setFriendlyUsername(e.target.value));
    }, [dispatch]);
    const handleInputPassword = useCallback((e: any) => {
        dispatch(createDialogActions.setPassword(e.target.value));
    }, [dispatch]);

    const separator = (margin: number = 50) => (<div style={{ marginBottom: margin }} />);

    return (
        <Modal
            modalHeading={`${isEditingTemplate ? 'Edit' : 'Add'} a ${mode === CreateMode.JOB ? "Job" : "Template"}`}
            modalLabel={mode === CreateMode.JOB ? "Jobs" : "Templates"}
            primaryButtonText={isEditingTemplate ? "Update" : "Create"}
            secondaryButtonText="Cancel"
            open={visible}
            onRequestSubmit={handleCreate}
            onRequestClose={() => dispatch(createDialogActions.setVisible(false))}>
            <TextInput
                labelText="Name"
                id="createinput-modal-internal-name"
                value={friendlyName}
                onInput={handleInputFriendlyName}
            />
            <TextInput
                labelText="Username"
                id="createinput-modal-internal-username"
                value={friendlyUsername}
                onInput={handleInputFriendlyUsername}
            />
            <TextInput
                labelText="Password"
                id="createinput-modal-internal-password"
                value={password}
                onInput={handleInputPassword}
                type="password"
            />

            {separator()}

            <p>Parameters:</p>
            {parametersPrototype.map((n: ConfigParameter) => {
                switch (n.type) {
                    case "STRING":
                        return (
                            <React.Fragment>
                                <TextInput
                                    labelText={n.friendlyName}
                                    id={`createinput-modal-string-${n.name}`}
                                    key={`createinput-modal-string-${n.name}`}
                                    value={parameters[n.name]}
                                    onChange={(e: any) => updateUserInputInModal(n, e)}
                                />
                                {separator(15)}
                            </React.Fragment>
                        );
                    case "INTEGER":
                        return (
                            <React.Fragment>
                                <NumberInput
                                    label={n.friendlyName}
                                    id={`createinput-modal-integer-${n.name}`}
                                    key={`createinput-modal-integer-${n.name}`}
                                    value={parameters[n.name]}
                                    onChange={(e: any) => updateUserInputInModal(n, e)}
                                />
                            </React.Fragment>
                        );
                    case "BOOLEAN":
                        return (
                            <React.Fragment>
                                <Checkbox
                                    id={`createinput-modal-boolean-${n.name}`}
                                    key={`createinput-modal-boolean-${n.name}`}
                                    onChange={(e: any) => updateUserInputInModal(n, e)}
                                    labelText={n.friendlyName}
                                    checked={parameters[n.name]}
                                />
                                {separator(15)}
                            </React.Fragment>
                        )
                    default: return null;
                }
            })}
        </Modal>
    );
}