import React, { useCallback, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { 
  HeaderContainer, 
  Header, 
  StructuredListCell,
  HeaderName, 
  HeaderNavigation, 
  HeaderMenuItem, 
  StructuredListWrapper, 
  StructuredListRow,
  StructuredListHead,
  StructuredListBody,
  Button
} from 'carbon-components-react';
import { Add32, TrashCan16,  Launch16, Edit16, Export16} from '@carbon/icons-react';
import { useShallowSelector } from '../utils';
import { createFromTemplate, deleteTemplateOrJob, loadDataFromServer, openJob, resetParameters, startEditTemplate } from '../redux/actions';
import { actions as appActions, AppTab } from '../redux/app-state';
import { actions as createDialogActions, CreateMode } from '../redux/create-dialog-state';
import { batchActions } from 'redux-batched-actions';

import CreateDialog from './CreateDialog';
import ErrorDialog from './ErrorDialog';

import '../App.css';
import LogsDialog from './LogsDialog';

function App() {
  const dispatch = useDispatch();
  const { server } = useShallowSelector(state => state);
  const { jobs, templates } = server;
  const { currentTab } = useShallowSelector(state => state.app);
  useEffect(() => {
    if(!server.isLoaded){
      dispatch(loadDataFromServer());
    }
  }, [server, dispatch]);

  const setSelectedTab = useCallback((newTab: AppTab) => {
    dispatch(appActions.setCurrentTab(newTab));
  }, [dispatch]);

  const openCreateDialog = useCallback(() => {
    dispatch(resetParameters());
    dispatch(batchActions([
      createDialogActions.setVisible(true),
      createDialogActions.setMode(currentTab === AppTab.JOBS_TAB ? CreateMode.JOB : CreateMode.TEMPLATE),
      createDialogActions.setFriendlyName(""),
      createDialogActions.setFriendlyUsername(""),
      createDialogActions.setPassword(""),
      createDialogActions.setEditTemplateID(-1),
    ]));
  }, [dispatch, currentTab]);

  const handleOpenDialog = useCallback((jobIndex: number) => {
    dispatch(openJob(jobs[jobIndex]));
  }, [dispatch, jobs]);

  const handleEditTemplate = useCallback((templateId: number) => {
    dispatch(startEditTemplate(templateId));
  }, [dispatch]);

  const handleCreateFromTemplate = useCallback((templateId: number) => {
    dispatch(createFromTemplate(templateId));
  }, [dispatch]);

  const deleteSelected = useCallback((id: number) =>  {
    dispatch(deleteTemplateOrJob(currentTab === AppTab.JOBS_TAB ? "job" : "template", id));
  }, [dispatch, currentTab]);

  return (
    <div className="App">
      <HeaderContainer
        render={() => (
        <Header style={{position: "relative"}} aria-label="Main Header">
          <HeaderName href="#" prefix="IBM-I">
            Auto Zap
          </HeaderName>
          <HeaderNavigation aria-label="Main Header">
            <HeaderMenuItem href="#" onClick={() => setSelectedTab(AppTab.JOBS_TAB)}>Jobs</HeaderMenuItem> 
            <HeaderMenuItem href="#" onClick={() => setSelectedTab(AppTab.TEMPLATES_TAB)}>Templates</HeaderMenuItem>
            <HeaderMenuItem href="#" onClick={openCreateDialog}><Add32 /></HeaderMenuItem>
          </HeaderNavigation>
        </Header>
      )}/>
      <div>
        <StructuredListWrapper>
          <StructuredListHead>
            <StructuredListRow head>
              <StructuredListCell head style={{width: "25%"}}>Name</StructuredListCell>
              <StructuredListCell head style={{width: "60%"}}>{currentTab === AppTab.JOBS_TAB && 'Date Created'}</StructuredListCell>
              <StructuredListCell head style={{width: "15%"}}>Controls</StructuredListCell>
            </StructuredListRow>
          </StructuredListHead>
          <StructuredListBody>
            {currentTab === AppTab.JOBS_TAB ? (
              jobs.map((n, i) => (
                <StructuredListRow key={`renderObject-key-${i}`}>
                  <StructuredListCell noWrap >{n.friendlyName}</StructuredListCell>
                  <StructuredListCell>{new Date(n.timeCreated + new Date().getTimezoneOffset() * 60 * 1000).toLocaleString()}</StructuredListCell>
                  {currentTab === AppTab.JOBS_TAB && (<StructuredListCell>
                    <Button 
                      small
                      renderIcon={Launch16}
                      kind="ghost"
                      iconDescription="Display Job Info"
                      hasIconOnly
                      onClick={() => handleOpenDialog(i)} />
                      <Button 
                      small
                      renderIcon={TrashCan16}
                      kind="ghost"
                      iconDescription="Delete Job"
                      hasIconOnly
                      onClick={() => deleteSelected(n.id)} />
                    </StructuredListCell>)}
                </StructuredListRow>
              ))
            ) : (
              templates.map((n, i) => (
                <StructuredListRow key={`renderObject-key-${i}`}>
                  <StructuredListCell noWrap >{n.friendlyName}</StructuredListCell>
                  <StructuredListCell>&nbsp;</StructuredListCell>
                  <StructuredListCell>
                  <Button 
                      small
                      renderIcon={Export16}
                      kind="ghost"
                      iconDescription="Create From Template"
                      hasIconOnly
                      onClick={() => handleCreateFromTemplate(n.id)} />
                    <Button 
                      small
                      renderIcon={Edit16}
                      kind="ghost"
                      iconDescription="Edit"
                      hasIconOnly
                      onClick={() => handleEditTemplate(n.id)} />
                    <Button 
                      small
                      renderIcon={TrashCan16}
                      kind="ghost"
                      iconDescription="Delete Template"
                      hasIconOnly
                      onClick={() => deleteSelected(n.id)} />
                    </StructuredListCell>
                </StructuredListRow>
              ))
            )}
          </StructuredListBody>
        </StructuredListWrapper>
      </div>
      <CreateDialog />
      <LogsDialog />
      <ErrorDialog />
    </div>
  );
}

export default App;
