export enum JobState {
    RUNNING, COMPLETED, ERRORED
};

export type ConfigParameter = {
    name: string;
    type: "STRING" | "BOOLEAN" | "INTEGER";
    default: any;
    friendlyName: string;
    friendlyUsername: string;
    password: string;
}

export type Template = {
    friendlyName: string;
    friendlyUsername: string;
    password: string;
    parameters: { [key: string]: any };
    id: number;
}

export type Job = {
    friendlyName: string;
    internalName: string;
    friendlyUsername: string;
    password: string;
    timeCreated: number;
    parameters: { [key: string]: any };
    id: number;
}

export type JobStatus = {
    jobName: string;
    jobLogs: string;
    jobState: "RUNNING" | "COMPLETED" | "ERRORED";
}

export type Status = {
    ok: boolean;
    log?: string;
}

export interface AutoZapInterface {
    getPossibleParameters(): Promise<ConfigParameter[]>;

    getTemplates(): Promise<Template[]>;
    getJobs(): Promise<Job[]>;
    getJobStatus(name: string): Promise<JobStatus | null>;

    createJob(name: string, username: string, password: string, params: { [key: string]: any }): Promise<Status>;
    createTemplate(name: string, username: string, password: string, params: { [key: string]: any }): Promise<Status>;

    renameTemplate(id: number, newName: string): Promise<Status>;
    updateTemplateParameter(id: number, parameter: string, value: any): Promise<Status>;

    deleteJob(id: number): Promise<Status>;
    deleteTemplate(id: number): Promise<Status>;
}

const req = <T>(url: string) => new Promise<T>((ok, fail) =>
    fetch(url)
        .then(n => n.json() as Promise<T>)
        .then(ok)
        .catch(fail));

const build = (url: string, params: { [key: string]: string }) => {
    let urla = new URL(url, window.location.origin);
    Object.entries(params).forEach(n => urla.searchParams.append(...n));
    return urla.toString();
}


const send = <T>(url: string, content: { [key: string]: any }) => new Promise<T>((ok, fail) =>
    fetch(url, {
        method: "POST",
        body: JSON.stringify(content),
        headers: {
            "Content-Type": "application/json",
        },
    })
        .then(n => n.json() as Promise<T>)
        .then(ok)
        .catch(fail));

class AutoZap implements AutoZapInterface {

    getPossibleParameters(): Promise<ConfigParameter[]> {
        return req<ConfigParameter[]>("/api/info/configParameters");
    }

    getTemplates(): Promise<Template[]> {
        return req<Template[]>("/api/templates/all");
    }

    getJobs(): Promise<Job[]> {
        return req<Job[]>("/api/jobs/all");
    }
    getJobStatus(name: string): Promise<JobStatus | null> {
        return req<JobStatus | null>(build("/api/jobs/status", { job: (name) }));
    }

    createJob(name: string, username: string, password: string, params: { [key: string]: any; }): Promise<Status> {
        return send<Status>("/api/jobs/create", { friendlyName: name, friendlyUsername: username, password: password, parameters: params });
    }

    createTemplate(name: string, username: string, password: string, params: { [key: string]: any; }): Promise<Status> {
        return send<Status>("/api/templates/create", { friendlyName: name, friendlyUsername: username, password: password, parameters: params });
    }

    updateTemplateParameter(id: number, parameter: string, value: any): Promise<Status> {
        return req<Status>(build("/api/templates/updateParameter", { id: id.toString(), configParameter: parameter, newValue: (value) }));
    }

    renameTemplate(id: number, newName: string): Promise<Status> {
        return req<Status>(build("/api/templates/rename", { id: id.toString(), newName: (newName) }));
    }

    deleteJob(id: number): Promise<Status> {
        return req<Status>(build("/api/jobs/delete", { id: id.toString() }));
    }

    deleteTemplate(id: number): Promise<Status> {
        return req<Status>(build("/api/templates/delete", { id: id.toString() }));
    }
}

const instance = new AutoZap();

export default instance;