import { Modal } from "carbon-components-react";
import { useCallback } from "react";
import { useDispatch } from "react-redux";
import { useShallowSelector } from "../utils";
import { actions as errorDialogActions } from '../redux/error-dialog-state';

export default function ErrorDialog(){
    const dispatch = useDispatch();

    const { visible, errorString } = useShallowSelector(state => state.errorDialog);
    const handleClose = useCallback(() => {
        dispatch(errorDialogActions.setVisible(false));
    }, [dispatch]);

    return (
        <Modal
            modalHeading="Error"
            modalLabel="The task given could not be completed."
            passiveModal
            open={visible}
            onRequestClose={handleClose}
        >
            <p style={{whiteSpace: 'pre-wrap'}}>{errorString}</p>
        </Modal>
    );
}